<template>
  <el-container class='layout_content'>
    <el-aside width='auto'>
      <aside-left class='aside_menu' :is-collapse='isCollapse' />
    </el-aside>
    <el-container>
      <el-header>
        <div>
          <i
            :class="{
              'el-icon-s-fold': isCollapse,
              'el-icon-s-unfold': !isCollapse,
            }"
            @click='isCollapse = !isCollapse'
          ></i>
          <span>新闻后台管理系统PC</span>
        </div>
        <el-autocomplete
          v-model="state"
          class="search"
          :fetch-suggestions="querySearchAsync"
          placeholder="请输入内容"
          @select="handleSelect"
        />
        <div>
          <el-button
            type='primary'
            @click="dialogVisible = true"
            circle
            icon='el-icon-chat-dot-round'
          />
          <el-button
            type='info'
            @click='onLogout'
            plain
            circle
            icon='el-icon-switch-button'
          />
        </div>
      </el-header>

      <el-dialog
        title='我不是robot'
        :visible.sync='dialogVisible'
        width='30%'
        append-to-body
        lock-scroll
        :close-on-click-modal="false"
        show-close>
        <div class="dialog">
          <div class="message-list" ref="message-list">
            <div
              class="message-item"
              :class="{ reverse: index%2 == 1 }"
              v-for="(item, index) in messages"
              :key="index"
            >
            <el-avatar
              class="avatar"
              :size="40"
              fit
              :src="photo(index)"/>
              <div class="msg-wrap" style="flex: 1;">
                <div class="title">{{ item }}</div>
              </div>
            </div>
          </div>
        </div>
        <el-input
          placeholder="请输入内容"
          class="send-message"
          v-model="message">
          <el-button
            slot="append"
            type="success"
            @click="onSend"
            round>发送</el-button>
        </el-input>
      </el-dialog>

      <el-main>
        <router-view />
      </el-main>
    </el-container>
  </el-container>
</template>

<script>

import AsideLeft from './components/aside'
import { removeItem, getItem, setItem } from '@/utils/storage'
import axios from 'axios'
import { getSearchResult } from '@/api/article'
import { getUserProfile } from '@/api/user'
import { mapState } from 'vuex'
export default {
  name: 'LayoutIndex',
  components: {
    AsideLeft
  },
  props: {},
  data () {
    return {
      isCollapse: false,
      restaurants: getItem('searchtext') || [],
      state: '',
      timeout: null,
      dialogVisible: false,
      messages: getItem('messages') || ['你好'],
      message: '',
      robotPhoto: 'https://img01.yzcdn.cn/vant/logo.png',
      userphoto: '',
      searchtext: 'q'
    }
  },
  computed: {
    ...mapState(['user'])
  },
  watch: {},
  created () {
    this.loadCurrentUser()
    this.search(this.searchtext)
  },
  mounted () {},
  methods: {
    onLogout () {
      this.$confirm('是否继续退出?', '退出提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          removeItem('user')
          this.$router.push('/login')
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '退出已取消'
          })
        })
    },
    querySearchAsync (queryString, cb) {
      var restaurants = this.restaurants
      var results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants

      clearTimeout(this.timeout)
      this.timeout = setTimeout(() => {
        cb(results)
      }, 3000 * Math.random())
    },
    createStateFilter (queryString) {
      this.search(queryString)
      return (state) => {
        return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
      }
    },
    async search (queryString) {
      const { data } = await getSearchResult({
        page: 1,
        per_page: 50,
        q: queryString
      })
      const { results } = data.data
      this.restaurants.splice(0, 10)
      results.map((value, index, arry) => {
        this.restaurants.push(
          { id: value.id, value: value.title }
        )
      })
      setItem('searchtext', this.restaurants)
    },
    handleSelect (item) {
      this.$router.push(`/publish?id=${item.id}`)
    },
    photo (index) {
      if (index % 2 === 1) {
        return this.userphoto
      }
      return this.robotPhoto
    },
    async loadCurrentUser () {
      const { data } = await getUserProfile()
      this.userphoto = data.data.photo
    },
    onSend () {
      const message = this.message.trim()
      if (!message) {
        return
      }
      this.onloadAxios()
      this.messages.push(this.message)
      this.message = ''
    },
    async onloadAxios () {
      try {
        const res = await axios.post(
          `http://www.tuling123.com/openapi/api?key=61d2ef6a5c38407f8d58df05ea992df7&info=${this.message}`
        )
        this.messages.push(res.data.text)
      } catch (error) {
        this.messages.push('你网咯不好,不想和你说话')
      }
      setItem('messages', this.messages)
    }
  }
}
</script>

<style scoped lang='less'>
.layout_content {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  .el-aside {
    background-color: #d3dce6;
    color: #333;
    text-align: center;
    line-height: 200px;
    .aside_menu {
      height: 100%;
      transition: all 0.5s;
      overflow: hidden;
    }
  }
  .el-header {
    background-color: #ffffff;
    color: #333;
    border-bottom: 1px solid #888888;
    text-align: center;
    line-height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .search{
      width: 30%;
    }
  }
  .el-main {
    background-color: #e9eef3;
    color: #333;
  }
}
.dialog{
  height: 50vh;
  .message-list {
    height: 100%;
    overflow-y: scroll;
    .message-item {
      display: flex;
      flex-flow: row nowrap;
      align-items: flex-start;
      padding: 10px;
      .msg-wrap {
        word-break: break-all;
        word-wrap: break-word;
      }
      .title {
        display: table;
        background: #e0effb;
        border-radius: 6px;
        font-size: 14px;
        padding: 10px 10px;
        margin-right: 45px;
      }
      .avatar {
        width: 40px;
        height: 40px;
        margin-right: 5px;
      }
    }
    .reverse {
      flex-direction: row-reverse;
      .msg-wrap {
        display: flex;
        flex-direction: row-reverse;
        .title {
          margin-right: 5px;
          margin-left: 45px;
        }
      }
    }
  }

  .send-message {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: #f5f5f6 !important;
  }
}
</style>
